@mixin pad {
    @media screen and (max-width: $pad-width) {
        @content;
    }
}

@mixin mobile {
    @media screen and (max-width: $mobile-width) {
        @content;
    }
}

//  ham-menu
@mixin ham-menu {
    //  ham-menu
    .ham-menu {
        display: block;
        width: 40px;
        height: 40px;
        border-radius: 20%;
        background: $ham-menu-bg-primary;
    }
    //  ham-menu
    .ham-frame {
        position: relative;
        top: 50%;
    }
    //  ham-menu 
    .ham-bar {
        height: 3px;
        width: 30px;
        background-color: $ham-icon-bar-primary;
        position: absolute;
        left: 5px;
    }
    .ham-bar--top {
      transform: translateY(-7px);
    }
    .ham-bar--bottom {
      transform: translateY(7px);
    }
    //  ham-menu checked
    .ham-toggle:checked ~ .ham-menu .ham-frame .ham-bar--middle {
      opacity: 0;
    }
    .ham-toggle:checked ~ .ham-menu .ham-frame .ham-bar--top {
      transform: rotate(-45deg);
    }
    .ham-toggle:checked ~ .ham-menu .ham-frame .ham-bar--bottom {
      transform: rotate(45deg);
    }
    .header-nav {
        width: 100%;
        text-align: center;
        background-color: $ham-icon-bg-primary;
        position: absolute;
        z-index: 9999;
        top: 65px;
        left: 0%;
        // left: -100%; //
        transform: translateY(-200%);
        transition: transform .5s;
    }
    .ham-toggle:checked ~ .header-nav {
        // left: 0%; //checked
        transform: none;
    }
    .list-menu {
        display: block;
        flex-direction: column;
    }
    .list-menu-item {
        @include pad {
            width: 100%;
        }
    }
    .list-menu-item-link {
        width: 100%;
        padding: 12px 0;
    }
}
// padham-menu
@media screen and (max-width: $pad-width) {
    @include ham-menu;
}

